import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Spin, Table, message } from 'antd';
import { isEmpty   } from 'lodash';
import { CartHeadAction } from 'components/common';
import { BtnExport } from 'components/common/button';
import { getFormerStaff, exportFormerStaffsExcel } from 'services/staffFormer';
import FormSearchStaffProfile from './FormSearchStaffProfile';

class StaffFormer extends Component {
  state = {
    data: [],
    loading: false,
    btnExportLoading: false,
    searchParams: {}
  };

  convertToTableTreeData = rawData => (
    rawData.map(staffByYear => (
      {
        year: staffByYear.year,
        children: staffByYear.staffProfiles
      }
    ))
  )

  handleExportExcel = async() => {
    this.setState({ btnExportLoading: true });
    try {
      await exportFormerStaffsExcel(this.state.searchParams);
    } catch (error) {
      message.error('Có lỗi xãy ra');
    }
    this.setState({ btnExportLoading: false });
  }

  handleSearch = async(params) => {
    this.setState({ loading: true });
    try {
      const response = await getFormerStaff(params);
      if(response.data && response.data.items) {
        this.setState({
          searchParams: params,
          data: this.convertToTableTreeData(response.data.items)
        })
      }
    } catch (error) {
      message.error('Có lỗi xãy ra');
    }
    this.setState({ loading: false });
  }

  render() {
  
    const columns = [
      {
        title: 'Năm',
        dataIndex: 'year',
        key: 'year',
        width: '100px',
        align: 'center'
      },
      {
        title: 'Mã',
        dataIndex: 'staffId',
        key: 'staffId',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
        align: 'center'
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
        align: 'center'
      },
      {
        title: 'Ngày tuyển dụng',
        dataIndex: 'recruitedDate',
        key: 'recruitedDate',
        align: 'center'
      },
      {
        title: 'Bậc đào tạo',
        dataIndex: 'educationLevel',
        key: 'educationLevel',
        align: 'center'
      },
      {
        title: 'Danh hiệu cao nhất',
        dataIndex: 'title',
        key: 'title',
        align: 'center'
      },
      {
        title: 'Chức vụ',
        dataIndex: 'position',
        key: 'position',
        align: 'center'
      },
    ];

    const { data, btnExportLoading } = this.state;

    return (
      <Fragment>
        <Spin spinning={this.state.loading}>
          <Card title="Danh mục chức vụ">
            <CartHeadAction>
              <BtnExport
                title="Xuất Excel"
                onClick={this.handleExportExcel}
                disabled={isEmpty(data)}
                loading={btnExportLoading}
              />
            </CartHeadAction>
            <Row>
              <Col span={24}>
                <FormSearchStaffProfile dataForm={this.dataFormSeach} onSearch={this.handleSearch} />
              </Col>
            </Row>
            <Row>
              <Col>
                <Table columns={columns} dataSource={data} bordered />
              </Col>
            </Row>
          </Card>
        </Spin>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <StaffFormer />
  </Page>
);



