import { request, requestDownload } from 'utils';
import { join } from 'lodash';

const prefix = '/api/organization';

export const getFormerStaff = ({
  staffId,
  fullname,
  years = [],
  gender,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) => {
  let url = '/StaffProfile/LeaveStaffProfile';
  if(years.length > 0) {
    url += `?${join(years.map(year => `years=${year}`), '&')}`;
  }
  console.log(url);
  return request({
    prefix,
    url,
    method: 'GET',
    params: {
      staffId,
      fullname,
      gender,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });
}

export const exportFormerStaffsExcel = ({
  staffId,
  fullname,
  years = [],
  gender
} = {}) =>
  requestDownload({
    defaultFileName: 'LeaveStaffProfileExcel.xlsx',
    prefix,
    url: '/StaffProfile/ExportLeaveStaffProfileExcel',
    method: 'POST',
    data: {
      staffId,
      fullname,
      years,
      gender
    }
  });
  


